<template>
  <div class="search_container">
    <!-- 搜索栏 -->
    <van-search
      class="search_ipt"
      v-model="keyWords"
      shape="round"
      show-action
      clearable
      background="#4fc08d"
      placeholder="请输入关键词"
      @search="onSearch"
      @cancel="$router.back()"
      @focus="isResultShow = false"
    />

    <!-- 搜索结果 -->
    <search-result v-if="isResultShow" :searchText="keyWords" />

    <!-- 联想建议 -->
    <search-association
      v-else-if="keyWords"
      :searchText="keyWords"
      @search="onSearch"
    />

    <!-- 历史记录 -->
    <search-history
      v-else
      @search="onSearch"
      @deleteAllHistory="searchHistory = $event"
      :searchHistory="searchHistory"
    />
  </div>
</template>

<script>
import { setItem, getItem } from '@/utils/storage.js';
import { mapState } from 'vuex';
import Association from './components/association.vue';
import History from './components/history.vue';
// import { getSearchHistory } from "@/api/search.js";

export default {
  name: 'SearchPage',
  components: {
    SearchAssociation: () => import('./components/association.vue'),
    SearchHistory: () => import('./components/history.vue'),
    SearchResult: () => import('./components/result.vue'),
  },

  data() {
    return {
      keyWords: '', // 关键词
      isResultShow: false, // 是否显示搜索结果
      searchHistory: [], // 搜索历史记录
    };
  },
  computed: {
    ...mapState(['userToken']),
  },
  watch: {
    searchHistory() {
      setItem('search_history', this.searchHistory);
    },
  },
  methods: {
    // 确定搜索时触发
    onSearch(value) {
      this.keyWords = value;
      const ind = this.searchHistory.indexOf(value);
      if (ind !== -1) {
        this.searchHistory.splice(ind, 1);
      }
      // 将搜索的历史 存储在本地
      this.searchHistory.unshift(value);
      // setItem("search_history", this.searchHistory);
      this.isResultShow = true;
    },

    // 获取并处理用户历史记录
    async userHistory() {
      // 从本地获取历史记录
      const search = getItem('search_history') || [];
      // 若果已登录
      // if (this.userToken) {
      //   let totalHistory = [];
      //   try {
      //     //从后台获取历史记录
      //     let { data } = await getSearchHistory();
      //     let { keywords } = data.data; //后台存储的历史记录

      //     //若果本地有历史记录 合并后台历史记录
      //     if (search.length) {
      //       totalHistory = search.concat(keywords);
      //       let arr = new Set(totalHistory);
      //       this.searchHistory = [...arr];
      //     } else {
      //       this.searchHistory = keywords;
      //     }
      //   } catch (e) {
      //     console.log(e);
      //   }
      // } else {
      // 如果未登录
      this.searchHistory = search;
      // }
    },
  },
  created() {
    this.userHistory();
  },
};
</script>

<style lang="less" scoped>
.search_ipt {
  /deep/.van-field__control:focus {
  }
}
</style>
