<template>
  <div class="act_container">
    <van-cell
      v-for="(item, index) in assWords"
      :key="index"
      icon="search"

      @click="$emit(`search`, item)"
    >
      <div slot="title" v-html="htmlStr(item)"></div>
    </van-cell>
  </div>
</template>

<script>
import { getSearchAssociation } from '@/api/search.js';
import { debounce } from 'lodash'; // 引入第三方  防抖包

export default {
  name: 'Association',
  props: {
    searchText: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      assWords: [],
    };
  },
  timer: null,
  filter: {},
  watch: {
    // 监听搜索关键词变化  请求联想数据
    searchText: {
      handler: debounce(async function () {
        try {
          const { data } = await getSearchAssociation({ q: this.searchText });
          this.assWords = data.data.options;
        } catch (e) {
          console.log(e);
        }
      }, 300),

      immediate: true,
    },
  },
  methods: {
    htmlStr(str) {
      return str.replace(
        new RegExp(this.searchText, 'gi'),
        `<span style="color:red">${this.searchText}</span>`,
      );
    },
  },
  created() {},
};
</script>

<style lang="less" scoped>
</style>
