<template>
  <div class="hsty_container">
    <van-cell title="历史记录">
      <div class="right_text" v-if="isDelete">
        <span @click="$emit(`deleteAllHistory`, [])">全部删除</span>
        &nbsp;
        <span @click="isDelete = false">完成</span>
      </div>
      <van-icon
        v-else
        @click="isDelete = true"
        slot="right-icon"
        :name="searchHistory.length ? `delete` : ``"
      />
    </van-cell>
    <van-cell
      v-for="(item, index) in searchHistory"
      :key="index"
      :title="item"
      @click="$emit(`search`, item)"
    >
      <van-icon
        v-if="isDelete"
        @click.stop="removeHistory(index)"
        slot="right-icon"
        name="close"
      />
    </van-cell>
  </div>
</template>

<script>
// import { setItem } from "@/utils/storage.js";
export default {
  name: 'History',
  props: {
    searchHistory: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      // 是否显示删除状态
      isDelete: false,
    };
  },
  methods: {
    // 删除单条记录
    removeHistory(index) {
      this.searchHistory.splice(index, 1);
      // 将本地历史记录更新
      // setItem("search_history", this.searchHistory);
    },
  },
};
</script>

